import interfaces from '../../utils/interfaces.js';
var common = require('../../utils/common.js');
import app from "../../App.vue";
export default {
	data() {
		return {
			storename:'',
			trade:''
		}
	},
	onLoad(options) {
		this.storename = options.storename;
	},
	onShow: function() {
		if (app.globalData.user_info) {
			this.repairtrade();
		} else {
			wx.navigateTo({
				url: '/pages/index/index',
			})
			return;
		}
	},
	methods: {
		repairtrade() {
			var storeID = app.globalData.user_info.storeID;
			wx.request({
				url: interfaces.repairTrade,
				data: {
					storeID: storeID
				},
				success: res => {
					this.trade=res.data;
				}
			})
		},
		goOrder(servername,serverid) {
			wx.redirectTo({
				url: '../order/add?serverName=' + servername + '&serverid=' + serverid + '&storename=' + this.storename
			})
		},
	}
}
